.about-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 40px 20px;
    background: linear-gradient(145deg, #ffffff, #f5f5f5);
    border-radius: 20px;
    box-shadow:
            0 10px 30px rgba(0, 0, 0, 0.05),
            0 1px 4px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease;
}

.about-container:hover {
    transform: translateY(-5px);
}

.about-title {
    font-size: 2.8em;
    background: linear-gradient(120deg, #ff6b6b, #ff8e8e);
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;
    margin-bottom: 30px;
    font-weight: 800;
    letter-spacing: -0.5px;
}

.about-description {
    font-size: 1.2em;
    color: #546e7a;
    line-height: 1.8;
    max-width: 600px;
    margin: 0 auto;
    padding: 0 20px;
}

.about-description p {
    margin-bottom: 1.5em;
}

@media (min-width: 768px) {
    .about-container {
        padding: 70px 50px;
    }

    .about-description {
        font-size: 1.3em;
    }
}

@media (prefers-reduced-motion: reduce) {
    .about-container {
        transition: none;
        transform: none;
    }
}