.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FFFFFF; /* Light background */
    padding: 0px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.header-title {
    font-size: 2em;
    color: #ff6b6b; /* Bright color for text */
    margin: 0;
    font-weight: bold;
    text-align: center;
}
img {
    width: 150px;
}

.nav {
    display: flex;
    gap: 15px;
    margin-top: 10px;
}

.nav-link {
    font-size: 1.1em;
    color: #4f4f4f;
    text-decoration: none;
    position: relative;
    padding: 5px 10px;
}

.nav-link::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -3px; /* Adjust position if needed */
    width: 0;
    height: 2px;
    background-color: #023574; /* Color of the underline */
    transition: width 0.3s ease;
}

.nav-link:hover::after {
    width: 100%;
}
.nav-link:hover {
    color: #023574;
    transform: scale(1.1); /* Increase the size to 110% */
    transition: transform 0.3s ease, color 0.3s ease; /* Smooth animation */
}

.language-selector {
    display: flex;
    gap: 10px;
    border-radius: 5px; /* Gives a similar rounded look */
    background-color: #f0f0f0; /* Light background to match */
    height: 100%; /* Makes it the same height as the nav links */
}

.language-flag {
    width: 30px;
    height: 30px;
}

.dropdown {
    position: absolute;
    top: 40px; /* Position below the main flag */
    right: 0;
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 4px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    padding: 5px;
    display: flex;
    flex-direction: column;
    gap: 5px;
    z-index: 10;
}

.dropdown-flag {
    width: 24px;
    height: 24px;
    cursor: pointer;
    transition: transform 0.2s ease;
}

.dropdown-flag:hover {
    transform: scale(1.1);
}