/* Base styles */
.home-container {
    min-height: 100vh;
    background-color: #f8fafc;
    overflow-x: hidden;
}

/* Hero Section */
.hero-section {
    min-height: 100vh;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    opacity: 1;
    transform: translateY(0);
    transition: opacity 0.5s ease, transform 0.5s ease;
    will-change: opacity, transform;
}

/* Video Background */
.video-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 1;
}

.video-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3); /* Adjust opacity as needed */
    z-index: 2;
}

.background-video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    transform: translateX(-50%) translateY(-50%);
    object-fit: cover;
    z-index: 1;
}

/* Add this class when scrolling starts */
.hero-section.fade-out {
    opacity: 0;
    transform: translateY(-50px);
    pointer-events: none;
}

.hero-content {
    text-align: center;
    z-index: 10;
    max-width: 600px;
    margin: 0 auto;
    position: relative;
}

.hero-title {
    font-size: 4rem;
    font-weight: 700;
    color: #ffffff;
    margin-bottom: 1rem;
    margin-top: -5rem; /* Move it up slightly */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
    font-family: "Dancing Script", cursive;
}

.hero-subtitle {
    font-size: 2.4rem;
    color: #ffffff;
    margin-bottom: 2rem;
    margin-top: -2rem; /* Move it up slightly */
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
    font-family: "Dancing Script", cursive;
}


/* Bounce animation for arrow */
.bounce-icon {
    animation: bounce 2s infinite;
}

.arrow-icon {
    color: #ffffff;
    width: 32px;
    height: 32px;
    filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.3));
}

/* Stats Section */
.stats-section {
    min-height: 100vh;
    background-color: white;
    padding: 4rem 2rem;
    position: relative;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 0.8s ease, transform 0.8s ease;
}

.stats-section.animate-in {
    opacity: 1;
    transform: translateY(0);
}

.stats-content {
    max-width: 1200px;
    margin: 0 auto;
}

.vision-title {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
    color: #023574;
    margin-bottom: 4rem;
}
.vision_subtitle {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
    color: #023574;
    margin-bottom: 4rem;
    font-family: "Dancing Script", cursive;

}

.stats-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
}

.stat-item {
    background: white;
    padding: 2rem;
    border-radius: 1rem;
    text-align: center;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.stat-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

.stat-icon {
    color: #7c3aed;
    margin-bottom: 1rem;
}

.stat-number {
    font-size: 2rem;
    font-weight: 700;
    color: #1a237e;
    margin-bottom: 0.5rem;
}

.stat-text {
    color: #64748b;
}

/* Animations */
@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-20px);
    }
    60% {
        transform: translateY(-10px);
    }
}

/* Media Queries */
@media (max-width: 768px) {
    .hero-title {
        font-size: 2.5rem;
    }

    .stats-grid {
        grid-template-columns: 1fr;
    }
}